body {
    font-family: sans-serif;
    margin: 0;
    padding: 0;
    line-height: 1.5;
 }
 
 /* Container */
 .container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
    box-sizing: border-box;
 }
 
 .container-fluid {
    width: 100%;
    margin: 0;
    padding: 0 15px;
    box-sizing: border-box;
 }
 
 /* Grid system */
 .row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
 }
 
 /* Grid columns */
 .grid-col {
    padding: 0 15px;
    box-sizing: border-box;
 }
 
 /* Grid spans for 12-column grid */
 .grid-span-1 { width: 8.333333%; }
 .grid-span-2 { width: 16.666667%; }
 .grid-span-3 { width: 25%; }
 .grid-span-4 { width: 33.333333%; }
 .grid-span-5 { width: 41.666667%; }
 .grid-span-6 { width: 50%; }
 .grid-span-7 { width: 58.333333%; }
 .grid-span-8 { width: 66.666667%; }
 .grid-span-9 { width: 75%; }
 .grid-span-10 { width: 83.333333%; }
 .grid-span-11 { width: 91.666667%; }
 .grid-span-12 { width: 100%; }
 
 /* Padding utility classes */
 .p-0 { padding: 0 !important; }
 .pt-0 { padding-top: 0 !important; }
 .pb-0 { padding-bottom: 0 !important; }
 .p-1 { padding: 0.25rem !important; }
 .pt-1 { padding-top: 0.25rem !important; }
 .pb-1 { padding-bottom: 0.25rem !important; }
 .p-2 { padding: 0.5rem !important; }
 .pt-2 { padding-top: 0.5rem !important; }
 .pb-2 { padding-bottom: 0.5rem !important; }
 .p-3 { padding: 1rem !important; }
 .pt-3 { padding-top: 1rem !important; }
 .pb-3 { padding-bottom: 1rem !important; }
 .p-4 { padding: 1.5rem !important; }
 .pt-4 { padding-top: 1.5rem !important; }
 .pb-4 { padding-bottom: 1.5rem !important; }
 .p-5 { padding: 3rem !important; }
 .pt-5 { padding-top: 3rem !important; }
 .pb-5 { padding-bottom: 3rem !important; }
 
 /* Form styles */
 .form-embed-container {
    margin-bottom: 20px;
 }
 
 /* Form controls */
 .form-group {
    margin-bottom: 1rem;
 }
 
 input, textarea {
    display: block;
    width: -webkit-fill-available;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    margin-bottom: 0.5rem;
 }
 
 input:focus, textarea:focus, select:focus {
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
 }
 
 /* Labels */
 label {
    display: inline-block;
    margin-bottom: 0.5rem;
    font-weight: 500;
 }
 
 .required-marker {
    color: #dc3545;
    margin-left: 0.25rem;
 }
 
 /* Checkboxes and radios */
 .form-check-group, .form-radio-group {
    margin-bottom: 1rem;
 }
 
 .form-check {
    position: relative;
    display: block;
    padding-left: 1.25rem;
    margin-bottom: 0.5rem;
 }
 
 .form-check-input {
    position: absolute;
    margin-top: 0.3rem;
    margin-left: -1.25rem;
    width: auto;
 }
 
 .form-check-label {
    margin-bottom: 0;
    font-weight: normal;
 }
 
 /* Select dropdowns */
 .form-select {
    display: block;
    width: 100%;
    padding: 0.375rem 2.25rem 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-position: right 0.75rem center;
    background-size: 16px 12px;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    appearance: none;
    margin-bottom: 0.5rem;
 }
 
 /* Error messages */
.invalid-feedback {
    display: none;
    width: 100%;
    margin-top: 0.25rem;
    font-size: 0.875rem;
    color: #dc3545;
    padding: 0.375rem 0.75rem;
    background-color: rgba(220, 53, 69, 0.05);
    border-radius: 0.25rem;
    border-left: 3px solid #dc3545;
}

/* Show the invalid feedback when the field has the is-invalid class */
.is-invalid ~ .invalid-feedback,
.is-invalid + .invalid-feedback {
    display: block;
}

/* Style for invalid form controls */
.form-control.is-invalid,
.form-select.is-invalid,
.form-check-input.is-invalid {
    border-color: #dc3545;
    padding-right: calc(1.5em + 0.75rem);
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-repeat: no-repeat;
    background-position: right calc(0.375em + 0.1875rem) center;
    background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
}

.form-control.is-invalid:focus,
.form-select.is-invalid:focus {
    border-color: #dc3545;
    box-shadow: 0 0 0 0.25rem rgba(220, 53, 69, 0.25);
}

.form-check-input.is-invalid ~ .form-check-label {
    color: #dc3545;
}

/* Form messages and alerts */
.form-messages {
   margin-bottom: 1.5rem;
   width: 100%;
}

.alert {
   position: relative;
   padding: 1rem;
   margin-bottom: 1rem;
   border: 1px solid transparent;
   border-radius: 0.375rem;
}

.alert-danger {
   color: #721c24;
   background-color: #f8d7da;
   border-color: #f5c6cb;
   animation: fadeIn 0.3s ease-in-out;
}

.alert-success {
   color: #155724;
   background-color: #d4edda;
   border-color: #c3e6cb;
   animation: fadeIn 0.3s ease-in-out;
}

/* Validation summary styles */
.validation-summary {
   display: none;
   background-color: #f8d7da;
   border: 1px solid #f5c6cb;
   border-radius: 0.375rem;
   padding: 1rem;
   margin-bottom: 1.5rem;
   color: #721c24;
}

.validation-summary[style*="display: block"] {
    display: block !important;
    animation: fadeIn 0.3s ease-in-out;
}

.validation-heading {
    font-size: 1.1rem;
    margin-top: 0;
    margin-bottom: 0.75rem;
    font-weight: 600;
}

.validation-error-list {
    margin: 0;
    padding-left: 1.5rem;
}

.validation-error-list li {
    margin-bottom: 0.5rem;
}

.validation-error-list a {
    color: #721c24;
    text-decoration: underline;
    font-weight: 500;
}

.validation-error-list a:hover {
    color: #4f1319;
}

@keyframes fadeIn {
    from { opacity: 0; transform: translateY(-10px); }
    to { opacity: 1; transform: translateY(0); }
}

 /* Form Documentation Styles (Author Mode) */
/* Configuration Toggle Button */
.config-toggle-container {
    margin-bottom: 1rem;
    text-align: right;
}

.config-toggle {
    background-color: #e9ecef;
    color: #495057;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    padding: 0.375rem 0.75rem;
    font-size: 0.875rem;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.config-toggle:hover {
    background-color: #dee2e6;
}

.config-toggle.active {
    background-color: #007bff;
    color: #fff;
    border-color: #007bff;
}

.form-documentation {
    background-color: #f8f9fa;
    border: 1px solid #dee2e6;
    border-radius: 0.5rem;
    padding: 1.5rem;
    margin-bottom: 2rem;
    font-size: 0.9rem;
}

.form-documentation h2 {
    color: #0056b3;
    margin-top: 0;
    margin-bottom: 1rem;
    font-size: 1.5rem;
}

.form-documentation h3 {
    color: #495057;
    margin-top: 1.5rem;
    margin-bottom: 1rem;
    font-size: 1.25rem;
    border-bottom: 1px solid #dee2e6;
    padding-bottom: 0.5rem;
}

.form-documentation h4 {
    color: #495057;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
    font-size: 1.1rem;
}

.config-table {
    font-size: 0.9rem;
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 1.5rem;
}

.config-table th {
    background-color: #e9ecef;
    text-align: left;
    padding: 0.75rem;
    border: 1px solid #dee2e6;
    font-weight: 600;
}

.config-table td {
    padding: 0.75rem;
    border: 1px solid #dee2e6;
    vertical-align: top;
}

.config-table tr:nth-child(even) {
    background-color: #f2f2f2;
}

.config-table tr:hover {
    background-color: #e2e6ea;
}

.config-sections {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    margin-top: 1rem;
}

.config-section {
    flex: 1 1 300px;
    background-color: #fff;
    border: 1px solid #e9ecef;
    border-radius: 0.25rem;
    padding: 1rem;
}

.config-section ul {
    padding-left: 1.25rem;
    margin-bottom: 0;
}

.config-section li {
    margin-bottom: 0.5rem;
}

.config-section li:last-child {
    margin-bottom: 0;
}

/* Buttons */
button, .btn {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    cursor: pointer;
}
 
 .btn-primary {
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
 }
 
 .btn-primary:hover {
    color: #fff;
    background-color: #0069d9;
    border-color: #0062cc;
 }
 
 .btn-secondary {
    color: #fff;
    background-color: #6c757d;
    border-color: #6c757d;
 }
 
 .btn-secondary:hover {
    color: #fff;
    background-color: #5a6268;
    border-color: #545b62;
 }
 
 .btn-success {
    color: #fff;
    background-color: #28a745;
    border-color: #28a745;
 }
 
 .btn-success:hover {
    color: #fff;
    background-color: #218838;
    border-color: #1e7e34;
 }
 
 .btn-danger {
    color: #fff;
    background-color: #dc3545;
    border-color: #dc3545;
 }
 
 .btn-danger:hover {
    color: #fff;
    background-color: #c82333;
    border-color: #bd2130;
 }
 
 .btn-block {
    display: block;
    width: 100%;
 }
 
 /* Form submit button container */
 .form-submit {
    margin-top: 1.5rem;
    text-align: right;
 }
 
 .form-submit .btn {
    min-width: 120px;
 }
 
 /* Visually hidden but accessible to screen readers */
 .visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
 }
 
 /* Responsive adjustments for small screens */
 @media (max-width: 768px) {
    .grid-col {
       width: 100% !important;
    }
 }
 
 .invalid-feedback {
    display: none;
