t-1 { padding-top: 0.25rem !important; }\n .pb-1 { padding-bottom: 0.25rem !important; }\n .p-2 { padding: 0.5rem !important; }\n .pt-2 { padding-top: 0.5rem !important; }\n .pb-2 { padding-bottom: 0.5rem !important; }\n .p-3 { padding: 1rem !important; }\n .pt-3 { padding-top: 1rem !important; }\n .pb-3 { padding-bottom: 1rem !important; }\n .p-4 { padding: 1.5rem !important; }\n .pt-4 { padding-top: 1.5rem !important; }\n .pb-4 { padding-bottom: 1.5rem !important; }\n .p-5 { padding: 3rem !important; }\n .pt-5 { padding-top: 3rem !important; }\n .pb-5 { padding-bottom: 3rem !important; }\n \n /* Form styles */\n .form-embed-container {\n    margin-bottom: 20px;\n }\n \n /* Form controls */\n .form-group {\n    margin-bottom: 1rem;\n }\n \n input, textarea {\n    display: block;\n    width: -webkit-fill-available;\n    padding: 0.375rem 0.75rem;\n    font-size: 1rem;\n    line-height: 1.5;\n    color: #495057;\n    background-color: #fff;\n    background-clip: padding-box;\n    border: 1px solid #ced4da;\n    border-radius: 0.25rem;\n    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;\n    margin-bottom: 0.5rem;\n }\n \n input:focus, textarea:focus, select:focus {\n    border-color: #80bdff;\n    outline: 0;\n    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);\n }\n \n /* Labels */\n label {\n    display: inline-block;\n    margin-bottom: 0.5rem;\n    font-weight: 500;\n }\n \n .required-marker {\n    color: #dc3545;\n    margin-left: 0.25rem;\n }\n \n /* Checkboxes and radios */\n .form-check-group, .form-radio-group {\n    margin-bottom: 1rem;\n }\n \n .form-check {\n    position: relative;\n    display: block;\n    padding-left: 1.25rem;\n    margin-bottom: 0.5rem;\n }\n \n .form-check-input {\n    position: absolute;\n    margin-top: 0.3rem;\n    margin-left: -1.25rem;\n    width: auto;\n }\n \n .form-check-label {\n    margin-bottom: 0;\n    font-weight: normal;\n }\n \n /* Select dropdowns */\n .form-select {\n    display: block;\n    width: 100%;\n    padding: 0.375rem 2.25rem 0.375rem 0.75rem;\n    font-size: 1rem;\n    line-height: 1.5;\n    color: #495057;\n    background-color: #fff;\n    background-image: url(\"data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e\");\n    background-repeat: no-repeat;\n    background-position: right 0.75rem center;\n    background-size: 16px 12px;\n    border: 1px solid #ced4da;\n    border-radius: 0.25rem;\n    appearance: none;\n    margin-bottom: 0.5rem;\n }\n \n /* Error messages */\n.invalid-feedback {\n    display: none;\n    width: 100%;\n    margin-top: 0.25rem;\n    font-size: 0.875rem;\n    color: #dc3545;\n    padding: 0.375rem 0.75rem;\n    background-color: rgba(220, 53, 69, 0.05);\n    border-radius: 0.25rem;\n    border-left: 3px solid #dc3545;\n}\n\n/* Show the invalid feedback when the field has the is-invalid class */\n.is-invalid ~ .invalid-feedback,\n.is-invalid + .invalid-feedback {\n    display: block;\n}\n\n/* Style for invalid form controls */\n.form-control.is-invalid,\n.form-select.is-invalid,\n.form-check-input.is-invalid {\n    border-color: #dc3545;\n    padding-right: calc(1.5em + 0.75rem);\n    background-image: url(\"data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 12 12' width='12' height='12' fill='none' stroke='%23dc3545'%3e%3ccircle cx='6' cy='6' r='4.5'/%3e%3cpath stroke-linejoin='round' d='M5.8 3.6h.4L6 6.5z'/%3e%3ccircle cx='6' cy='8.2' r='.6' fill='%23dc3545' stroke='none'/%3e%3c/svg%3e\");\n    background-repeat: no-repeat;\n    background-position: right calc(0.375em + 0.1875rem) center;\n    background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);\n}\n\n.form-control.is-invalid:focus,\n.form-select.is-invalid:focus {\n    border-color: #dc3545;\n    box-shadow: 0 0 0 0.25rem rgba(220, 53, 69, 0.25);\n}\n\n.form-check-input.is-invalid ~ .form-check-label {\n    color: #dc3545;\n}\n\n/* Form messages and alerts */\n.form-messages {\n   margin-bottom: 1.5rem;\n   width: 100%;\n}\n\n.alert {\n   position: relative;\n   padding: 1rem;\n   margin-bottom: 1rem;\n   border: 1px solid transparent;\n   border-radius: 0.375rem;\n}\n\n.alert-danger {\n   color: #721c24;\n   background-color: #f8d7da;\n   border-color: #f5c6cb;\n   animation: fadeIn 0.3s ease-in-out;\n}\n\n.alert-success {\n   color: #155724;\n   background-color: #d4edda;\n   border-color: #c3e6cb;\n   animation: fadeIn 0.3s ease-in-out;\n}\n\n/* Validation summary styles */\n.validation-summary {\n   display: none;\n   background-color: #f8d7da;\n   border: 1px solid #f5c6cb;\n   border-radius: 0.375rem;\n   padding: 1rem;\n   margin-bottom: 1.5rem;\n   color: #721c24;\n}\n\n.validation-summary[style*=\"display: block\"] {\n    display: block !important;\n    animation: fadeIn 0.3s ease-in-out;\n}\n\n.validation-heading {\n    font-size: 1.1rem;\n    margin-top: 0;\n    margin-bottom: 0.75rem;\n    font-weight: 600;\n}\n\n.validation-error-list {\n    margin: 0;\n    padding-left: 1.5rem;\n}\n\n.validation-error-list li {\n    margin-bottom: 0.5rem;\n}\n\n.validation-error-list a {\n    color: #721c24;\n    text-decoration: underline;\n    font-weight: 500;\n}\n\n.validation-error-list a:hover {\n    color: #4f1319;\n}\n\n@keyframes fadeIn {\n    from { opacity: 0; transform: translateY(-10px); }\n    to { opacity: 1; transform: translateY(0); }\n}\n\n /* Form Documentation Styles (Author Mode) */\n/* Configuration Toggle Button */\n.config-toggle-container {\n    margin-bottom: 1rem;\n    text-align: right;\n}\n\n.config-toggle {\n    background-color: #e9ecef;\n    color: #495057;\n    border: 1px solid #ced4da;\n    border-radius: 0.25rem;\n    padding: 0.375rem 0.75rem;\n    font-size: 0.875rem;\n    cursor: pointer;\n    transition: all 0.2s ease-in-out;\n}\n\n.config-toggle:hover {\n    background-color: #dee2e6;\n}\n\n.config-toggle.active {\n    background-color: #007bff;\n    color: #fff;\n    border-color: #007bff;\n}\n\n.form-documentation {\n    background-color: #f8f9fa;\n    border: 1px solid #dee2e6;\n    border-radius: 0.5rem;\n    padding: 1.5rem;\n    margin-bottom: 2rem;\n    font-size: 0.9rem;\n}\n\n.form-documentation h2 {\n    color: #0056b3;\n    margin-top: 0;\n    margin-bottom: 1rem;\n    font-size: 1.5rem;\n}\n\n.form-documentation h3 {\n    color: #495057;\n    margin-top: 1.5rem;\n    margin-bottom: 1rem;\n    font-size: 1.25rem;\n    border-bottom: 1px solid #dee2e6;\n    padding-bottom: 0.5rem;\n}\n\n.form-documentation h4 {\n    color: #495057;\n    margin-top: 1rem;\n    margin-bottom: 0.5rem;\n    font-size: 1.1rem;\n}\n\n.config-table {\n    font-size: 0.9rem;\n    width: 100%;\n    border-collapse: collapse;\n    margin-bottom: 1.5rem;\n}\n\n.config-table th {\n    background-color: #e9ecef;\n    text-align: left;\n    padding: 0.75rem;\n    border: 1px solid #dee2e6;\n    font-weight: 600;\n}\n\n.config-table td {\n    padding: 0.75rem;\n    border: 1px solid #dee2e6;\n    vertical-align: top;\n}\n\n.config-table tr:nth-child(even) {\n    background-color: #f2f2f2;\n}\n\n.config-table tr:hover {\n    background-color: #e2e6ea;\n}\n\n.config-sections {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 1.5rem;\n    margin-top: 1rem;\n}\n\n.config-section {\n    flex: 1 1 300px;\n    background-color: #fff;\n    border: 1px solid #e9ecef;\n    border-radius: 0.25rem;\n    padding: 1rem;\n}\n\n.config-section ul {\n    padding-left: 1.25rem;\n    margin-bottom: 0;\n}\n\n.config-section li {\n    margin-bottom: 0.5rem;\n}\n\n.config-section li:last-child {\n    margin-bottom: 0;\n}\n\n/* Buttons */\nbutton, .btn {\n    display: inline-block;\n    font-weight: 400;\n    text-align: center;\n    white-space: nowrap;\n    vertical-align: middle;\n    user-select: none;\n    border: 1px solid transparent;\n    padding: 0.375rem 0.75rem;\n    font-size: 1rem;\n    line-height: 1.5;\n    border-radius: 0.25rem;\n    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;\n    cursor: pointer;\n}\n \n .btn-primary {\n    color: #fff;\n    background-color: #007bff;\n    border-color: #007bff;\n }\n \n .btn-primary:hover {\n    color: #fff;\n    background-color: #0069d9;\n    border-color: #0062cc;\n }\n \n .btn-secondary {\n    color: #fff;\n    background-color: #6c757d;\n    border-color: #6c757d;\n }\n \n .btn-secondary:hover {\n    color: #fff;\n    background-color: #5a6268;\n    border-color: #545b62;\n }\n \n .btn-success {\n    color: #fff;\n    background-color: #28a745;\n    border-color: #28a745;\n }\n \n .btn-success:hover {\n    color: #fff;\n    background-color: #218838;\n    border-color: #1e7e34;\n }\n \n .btn-danger {\n    color: #fff;\n    background-color: #dc3545;\n    border-color: #dc3545;\n }\n \n .btn-danger:hover {\n    color: #fff;\n    background-color: #c82333;\n    border-color: #bd2130;\n }\n \n .btn-block {\n    display: block;\n    width: 100%;\n }\n \n /* Form submit button container */\n .form-submit {\n    margin-top: 1.5rem;\n    text-align: right;\n }\n \n .form-submit .btn {\n    min-width: 120px;\n }\n \n /* Visually hidden but accessible to screen readers */\n .visually-hidden {\n    position: absolute;\n    width: 1px;\n    height: 1px;\n    padding: 0;\n    margin: -1px;\n    overflow: hidden;\n    clip: rect(0, 0, 0, 0);\n    white-space: nowrap;\n    border: 0;\n }\n \n /* Responsive adjustments for small screens */\n @media (max-width: 768px) {\n    .grid-col {\n       width: 100% !important;\n    }\n }\n \n .invalid-feedback {\n    display: none;\n }"],"sourceRoot":""}]);
